import React from 'react'
import { Element } from 'react-scroll'
import Projects from '../Projects/Projects';
import "./Projectcontainer.css";

const Projectcontainer = () => {

    const projects=[{
        img: "data:image/jpeg;base64,",
        title:"JOB Board Platform",
        desc:"A job board platform connects employers with job seekers, enabling job postings and applications online.",
        link:"www.google.com",
    
    
},

{
    img: "https://www.rentallscript.com/resources/content/images/size/w1075h650/2021/10/Online-Car-Reservation-System.png",
    title:"Car rental system",
    desc:"A car rental system allows users to rent vehicles and manage bookings. It tracks car availability and handles payments.",
    link:"www.google.com",


},


{
    img: "data:image/jpeg;base64,",
    title:"Car Rental System",
    desc:"A job board platform connects employers with job seekers, enabling job postings and applications online.",
    link:"www.google.com",


},
];
  return (
    <Element className="projectcontainer" id="Projects" >
        <h1 >Projects</h1>
        <p>
            Here are some projects which I done during College Days.
        </p>
        <div className='projectcontainer__projects'>
        {  //flex-wrap=wrap;
        projects.map((project, index)=>{
              return (
               <Projects key={index}  img={project.img} title={project.title} desc={project.desc} link={project.link}/>
                )
            }
        )
    }
</div>
















        



    
    
</Element>
  
  )}

export default Projectcontainer